.game {
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
  align-items: center;
}

.game-info {
  margin-top: 16px;
  display: flex;
  width:100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.move-button  {
  width: 200px;
  background-color: rebeccapurple;
  border-width: 0px;
  border-radius: 5px;
  color: #fff;
  font-weight: 200;
  margin-bottom: 10px;
  padding: 10px 20px;
  text-decoration: none;
}